<template>
  <div id="app">

    <headerComponent></headerComponent>

    <main>
      <section class="bg-secondary">
        <b-container>
          <b-row>
            <b-col col offset="0" offset-xl="3" class="search_top">
              <b-form-input type="text" aria-label="Text input with checkbox" class="search_top__el1" />
              <b-form-input type="date" class="search_top__el2"></b-form-input>
              <b-form-select v-model="selected" :options="options" class="search_top__el3">
              </b-form-select>
              <b-button variant="success" class="search_top__btn">Подобрать яхту</b-button>
              <div class="search_top__favorites">f</div>
            </b-col>
          </b-row>

        </b-container>
      </section>
      <b-container>
        <b-row>

          <b-col lg="3">
            filters
          </b-col>

          <b-col lg="9">
            <div class="custom_map">
              <l-map ref="myMap">
              </l-map>
            </div>
            <b-breadcrumb :items="items" />
            <h2>аренда яхт на виргинских островах</h2>
            <p>Яхт-клуб «12 Узлов» предлагает аренду яхты на Виргинских островах – архипелаге в Карибском море, который
              разделён на три группы между Британией, США и Испанией. Аренда яхты на Виргинских островах является,
              пожалуй, лучшим возможным отдыхом, если вы любите солёный вкус свободы и открыты к зову путешествий.
              Компанией «12 Узлов» организована аренда яхт на Виргинских островах в соответствии с международными
              стандартами. К тому же мы готовы учесть все ваши пожелания.

              Базы: Тортола BVI (356), Тороролитола BVI (493),Тортола BVI (356), Тороролитола BVI (493),Тортола BVI
              (356), ...
              Типы яхт: парусная яхта (150), катамаран (200), моторная яхта (50)
              Полезная информация: Яхтинг на BVI, Яхтинг на севере BVI - Истрия, Аренда яхт на юге BVI</p>
          </b-col>

        </b-row>
      </b-container>
    </main>

    <footerComponent></footerComponent>

  </div>
</template>

<script>
  import headerComponent from './components/header.vue';
  import footerComponent from './components/footer.vue';
  import {
    LMap,
    LTileLayer,
    LMarker
  } from 'vue2-leaflet';

  export default {
    name: 'app',
    data() {
      return {
        selected: null,
        options: [{
            value: null,
            text: 'Please select some item'
          },
          {
            value: 'a',
            text: 'This is First option'
          },
          {
            value: 'b',
            text: 'Default Selected Option'
          },
          {
            value: 'c',
            text: 'This is another option'
          },
          {
            value: 'd',
            text: 'This one is disabled',
            disabled: true
          }
        ],
        items: [{
          text: 'Главная',
          href: '#'
        }, {
          text: 'Аренда яхт',
          href: '#'
        }, {
          text: 'Виргинские острова',
          active: true
        }]
      }
    },
    components: {
      LMap,
      LTileLayer,
      LMarker,
      headerComponent,
      footerComponent,
    },
    mounted() {
      this.$nextTick(() => {
        this.$refs.myMap.mapObject.ANY_LEAFLET_MAP_METHOD();
      })
    }
  }
</script>

<style lang="scss">
  @import '../src/scss/_fonts.scss';
  @import "../node_modules/bootstrap/scss/functions";
  @import '../src/scss/_variables.scss';
  @import "../node_modules/bootstrap/scss/mixins";
  // Optional
  @import "../node_modules/bootstrap/scss/root";
  @import "../node_modules/bootstrap/scss/reboot";
  @import "../node_modules/bootstrap/scss/type";
  @import "../node_modules/bootstrap/scss/grid";
  @import "../node_modules/bootstrap/scss/buttons";
  @import "../node_modules/bootstrap/scss/transitions";
  @import "../node_modules/bootstrap/scss/nav";
  @import "../node_modules/bootstrap/scss/navbar";
  @import "../node_modules/bootstrap/scss/breadcrumb";
  @import "../node_modules/bootstrap/scss/jumbotron";
  @import "../node_modules/bootstrap/scss/media";
  @import "../node_modules/bootstrap/scss/utilities";
  @import "../node_modules/bootstrap/scss/forms";
  @import "../node_modules/bootstrap/scss/images";
  @import "../node_modules/bootstrap/scss/custom-forms";
  @import "../node_modules/bootstrap/scss/pagination";
  @import "../node_modules/bootstrap/scss/tooltip";
  //Custom
  @import '../src/scss/_custom.scss';
  @import '../src/scss/_header.scss';
  @import '../src/scss/_homepage.scss';
  @import '../src/scss/_search.scss';
  @import '../src/scss/_footer.scss';
  @import '../src/scss/_map.scss';
</style>


